
import { defineComponent, toRefs, computed, onMounted } from "vue";
import { useRouter } from "vue-router";
import { useStore } from "vuex";

import FabricCard from "@/components/FabricCard.vue";
import TableTwoColumnCard from "@/components/TableTwoColumnCard.vue";
import TableCard from "@/components/TableCard.vue";
import { Column } from "@/constants/element";
import { getLastViewTimeString, loading } from "@/utils";

import {
  GetCollectionAnalyzeCompanyRank,
  GetCollectionAnalyzeObjectRank,
  GetCollectionAnalyzeRecentView
} from "@/typings/store";

const columns: Column[] = [
  { prop: "rank", label: "Rankings" },
  { prop: "company", label: "Company" },
  { prop: "count", label: "Click" }
];

const collectionRankingsColumns: Column[] = [
  // { prop: "rank", label: "Rankings", width: "90px" },
  { prop: "name", label: "Name" },
  { prop: "fabric", label: "Fabric", width: "120px" },
  // { prop: "color", label: "Color" },
  { prop: "real_composition", label: "Real composition" },
  { prop: "count", label: "Click", width: "100px", sort: true },
  { prop: "ctr", label: "CTR", width: "100px", sort: true }
];

const Component = defineComponent({
  name: "CollectionAnalysis",
  components: {
    FabricCard,
    TableCard,
    TableTwoColumnCard
  },
  props: {
    id: {
      type: Number,
      default: 0
    }
  },
  setup(props) {
    const router = useRouter();
    const store = useStore();
    const { id } = toRefs(props);

    const data = computed(() => store.state.collection.analysis.collection);

    const init = async () => {
      const loadingInstance = loading();
      try {
        await store.dispatch(GetCollectionAnalyzeRecentView, id.value);
        await store.dispatch(GetCollectionAnalyzeCompanyRank, id.value);
        await store.dispatch(GetCollectionAnalyzeObjectRank, id.value);
      } finally {
        loadingInstance.close();
      }
    };

    onMounted(async () => await init());

    const onCardClick = async (fabricId: number) => {
      await router.push({
        name: "FabricDetail",
        params: { id: fabricId }
      });
    };

    return {
      data,
      columns,
      collectionRankingsColumns,

      onCardClick,

      getLastViewTimeString
    };
  }
});

export default Component;
